@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;overflow-x:hidden;--primary-color: #757575;--secondary-color: #020077;--white: #fff;--water-blue: #AADAFF;--off-white: rgb(255, 255, 226);--dark-gray: #0e0e0e;--black: #0c0c0c;--error-color: #eb5757;--primary-font: "Consolas", "IBM Plex Mono", monospace;--secondary-font: poppins;--global-font-size: 16px}@media(max-width: 600px){html{--global-font-size: 18px}}.app{display:flex;flex-direction:column}header{flex:0 0}main{flex:1 0 600px;width:100%;margin:0 auto;padding:0 50px 20px 50px;display:flex;flex-direction:column}@media(max-width: 768px){main{padding:0}}h1,h2,h3,h4{font-family:"Consolas", "IBM Plex Mono", monospace;font-family:var(--primary-font)}h1{font-size:2rem;color:#fff;color:var(--white);text-align:center}h2{font-size:2.5rem;text-align:center;color:#757575;color:var(--primary-color)}@media(max-width: 680px){h2{font-size:1.5rem}}h3{font-size:1.5rem}h4{font-size:1.7rem;margin:5px}p{color:var(--light-gray);font-size:1.2rem}body{background:#fff;background:var(--white);color:var(--light-gray);font-family:"Consolas", "IBM Plex Mono", monospace;font-family:var(--primary-font);font-size:16px}.app{min-height:100vh;display:flex;flex-direction:column}@-webkit-keyframes rotate20{100%{-webkit-transform:rotateZ(-20deg) scale(1.2);transform:rotateZ(-20deg) scale(1.2)}}@keyframes rotate20{100%{-webkit-transform:rotateZ(-20deg) scale(1.2);transform:rotateZ(-20deg) scale(1.2)}}
